.is-hidden {
  display: none;
}

.hidden {
  display: flex;
}

a {
  color: #000;
  text-decoration: none;
}

p {
  margin: 0;
  padding-bottom: 10px;
}

img {
  height: 250px;
  width: 100%;
  display: block;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 60px;
  margin: 30px auto;
  display: flex;
}

.photo-card {
  width: calc((100% / 3)  - 90px);
  background-color: #f8f8f8;
  border-radius: 10px;
  flex-direction: column;
  transition: all .3s;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 1px 5px 5px #d7d7d7;
}

.photo-card:hover {
  background-color: #fdfdfd;
  transform: scale(1.05);
  box-shadow: 5px 5px 5px #9f9f9f;
}

.info {
  text-align: center;
  justify-content: space-around;
  padding: 5px 5px 0;
  font-size: 8px;
  display: flex;
}

.photo-card .info b {
  opacity: 0;
  color: #fff;
  font-size: 10px;
  transition: all .2s;
}

.photo-card:hover .info b {
  z-index: 200;
  opacity: 1;
  color: #000;
  position: relative;
}

input {
  border: 1px solid #000;
  border-radius: 5px;
  outline: none;
  padding: 10px;
}

.search-form {
  text-align: center;
  background-color: #a07171;
  padding: 35px;
}

button {
  color: #fff;
  background-color: #2b1d79;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 5px;
  padding: 10px 40px;
  font-size: 14px;
  font-weight: 400;
  transition: all .3s;
}

button:hover {
  cursor: pointer;
  color: #696868;
  background-color: #524390;
}

html {
  font-family: Helvetica, sans-serif;
  font-size: small;
}

.load-more {
  margin-left: auto;
  margin-right: auto;
}

.button-more {
  background-color: #a07171;
  padding-top: 25px;
  padding-bottom: 25px;
}

